<template>
	<view class="content">
		<view>
		</view>


		<button class="mini-btn" @click="connectWifi()" type="primary">连接AP</button>

		<button class="mini-btn" @click="tcpconnect()" type="primary">启动TCP</button>

		<button class="mini-btn" @click="tcpwrite()" type="primary">发送数据</button>
		<text>{{ms}}</text>

	</view>
</template>

<script>
	//创建一个 TCP Socket 实例
	const tcp = wx.createTCPSocket()
	export default {
		data() {
			return {
				wifissid: "",
				wifiBssid: "",
				SSID: 'esp_device_AP',
				password: 'hongwans',
				ms: "消息哦"
			}
		},
		onLoad() {
			var that = this //这里声明了that；将this存在that里面 

			uni.startWifi({
				success(res) {
					console.log(res)
				},
				fail(err) {
					console.log(err)
				}
			});

			wx.getConnectedWifi({
				success: function(e) {
					console.log(e.wifi, 'wifi获取成功')
					that.wifissid = e.wifi.SSID;
					that.wifiBssid = e.wifi.BSSID;

				},
				fail: function(e) {
					console.log(e, 'wifi获取失败')
				}
			});
		},
		methods: {
			//开始配网
			connectWifi() {
				var that = this //这里声明了that；将this存在that里面 

				wx.connectWifi({
					SSID: that.SSID,
					password: that.password,
					success(res) {
						that.ms += "连接AP成功\r\n";
						console.log(res)
					},
					fail(err) {
						console.log(err)
					}
				});


			},
			tcpconnect() {
				var that = this //这里声明了that；将this存在that里面 
				that.ms += "启动连接TCP\r\n";
				//启动连接
				tcp.connect({
					address: '192.168.1.1',
					port: 8899
				});

				tcp.onError(function(e) {
					that.ms += "连接错误\r\n" + e.errMsg;
				});

				//连接成功建立的时候触发该事件
				tcp.onConnect(function(e) {
					that.ms += "连接成功\r\n" + e.errMsg;

				});

			},
			tcpwrite() {
				var that = this //这里声明了that；将this存在that里面 
				//发送数据
				tcp.write("{\"on\":\"true\"}")
				that.ms += "发送数据\r\n";
			}

		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.mini-btn {
		width: 80%;
		margin-top: 20px;
		line-height: 2;
	}
</style>